<template>
  <div id="home-page">
    <header class="title">
      <p>Ryan's Website</p>
    </header>
    <div class="body">
      <p>Thank you for visiting. My name is Ryan Yi, and I am currently a Computer Science major at UCSD.
        I decided to create this website to help keep track of my projects, and have a place to post about my life.</p>
      <p>I currently have a strong interest in front end and mobile app development. I have made some Android apps in
        the past, and am currently working on a new one.</p>
      <p>Please feel free to take a look at some of my projects! I will be constatly updating this website,
      so I would be happy to hear any suggestions you have for me. I am currently working on making this website more mobile friendly.</p>
    </div>

    <br />
  </div>
</template>
<script>
  export default{
    name: 'HomePage'
  }
</script>

<style lang="scss" scoped>
  @import url('../styles/MainStyle.scss');
  #home-page{
    text-align: center;
  }
</style>



