<template>
  <div id="project-page">
    <header class="title" v-show="renderProject==-1">
      <p>Projects</p>
    </header>
    <div class="project-nav-instructions" v-show="renderProject==-1">
      <p>Click on a block to view the project. Click on the arrow of a project page to come back.</p>
    </div>

    <!--Render all of my projects as "blocks" on the website. This is done by reading from a json file.-->
    <div class="block">
      <div class="project-block" v-for="(project, index) in myProjects" :key="project" @click="renderProject = index"
      v-show="renderProject == -1">
        <div class="project-block-title">
          {{project.title}}
        </div>
      </div>

      <!--These will be the project descriptions. When a project box is clicked, this div will be rendered.
      This will have all of the information for a project.-->
      <div class="project-body" v-show="renderProject==index"
           v-for="(project, index) in myProjects" :key="project">
        <p class="title project-title">{{project.title}}</p>
        <div>
          <div class="top-project-back-arrow"><i class="fa fa-arrow-left" @click="returnToOriginal"></i></div>
          <i class="icon-java project-icons" v-show="project.language[0]"></i>
          <i class="icon-cplusplus project-icons" v-show="project.language[1]"></i>
          <i class="icon-c project-icons" v-show="project.language[2]"></i>
          <i class="icon-python project-icons" v-show="project.language[3]"></i>
          <i class="icon-html5 project-icons" v-show="project.language[4]"></i>
          <i class="icon-css3 project-icons" v-show="project.language[5]"></i>
          <i class="icon-javascript project-icons" v-show="project.language[6]"></i>
          <i class="icon-jquery project-icons" v-show="project.language[7]"></i>
          <i class="icon-bootstrap project-icons" v-show="project.language[8]"></i>
          <i class="icon-php project-icons" v-show="project.language[9]"></i>
          <i class="icon-go project-icons" v-show="project.language[10]"></i>
          <i class="icon-npm project-icons" v-show="project.language[11]"></i>
          <i class="fa fa-android project-icons" v-show="project.language[12]"></i>
        </div>
        <div class="project-date">{{project.date}}</div>
        <a v-bind:href="project.url" target="_blank" v-show="project.url != null" class="project-url">Website</a>
        <div class="project-content">{{project.content}}</div>
        <!--<div class="bottom-project-back-arrow"><i class="fa fa-arrow-left" @click="returnToOriginal"></i></div>-->
      </div>
    </div>
  </div>
</template>
<script>
  import 'font-mfizz/dist/font-mfizz.css';
  import Projects from '../Data/ProjectData.json';
  export default{
    name: 'ProjectPage',
    data(){
      return{
        /* myProjects is a JSON structured like so: {title, language, date}.

         * language is a boolean array where the index of the array corresponds to a language. The order is below.
         * Java, C++, C, Python, HTML5, CSS3, Javascript, jquery, Bootstrap, PHP, Go, npm, Android
         * More languages can be added if I learn new ones, but these are the main languages that my projects use.
         * As such, each value indicates whether the project uses the language at the specified index. */
        myProjects: [],
        renderProject: -1
      }
    },
    mounted() {
      this.myProjects = Projects;
    },
    methods: {
      returnToOriginal(){
        this.renderProject = -1;
        console.log(this.renderProject);
        this.stopPropagation();
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import url('../styles/MainStyle.scss');
  #project-page{
    .project-nav-instructions{
      color: #4f4e4e;
      font-size: 1.5em;
      font-family: Arial;
    }
    .block{
      .project-block{
        width: 48%;
        height: 15vh;
        float: left;
        margin-left: 1%;
        margin-bottom: 2%;
        background-color: #2eb0e8;

        .project-block-title{
          font-size: 3rem;
          text-align: center;
          line-height: 15vh;
          color: #e8e12c;
          text-shadow: -1px 0 #ede631, 0 1px #ede631, 1px 0 #ede631, 0 -1px #ede631;
        }


        @media (width: 400px){
          .project-block{
            float: none;
          }
        }

        .project-block-title:hover{
          cursor: pointer;
          color: #fff626;
          text-shadow: -1px 0 #fff728, 0 1px #fff728, 1px 0 #fff728, 0 -1px #fff728;
          background-color: #25bcfc;
        }
      }

      .project-body{
        padding-left: 5%;
        padding-right: 5%;

        .project-icons{
          font-size: 2rem;
        }

        .project-title{
          margin-bottom: 10px;
        }

        .project-date{
          font-size: 1rem;
          text-align: center;
        }

        .top-project-back-arrow{
          position: relative;
          text-align: left;
          bottom: 50px;
          float: left;
        }

        .top-project-back-arrow:hover{
          cursor: pointer;
        }

        .bottom-project-back-arrow{
          text-align: left;
          float: left;
        }

        .project-content{
          white-space: pre-wrap;
          word-wrap: break-word;
        }

        .project-url{
          font-size: 2rem;
        }

        .project-content{
          font-size: 1.5rem;
          text-align: left;
          padding-left: 2%;
          padding-right: 2%;
        }
      }
    }

    padding-left: 10%;
    padding-right: 10%;
    overflow: hidden;
  }
</style>



