<template>
  <div id="extra-page">
    <header class="title">
      <p>Extra</p>
    </header>

    <div class="body" id="not-main-dialogue">
      <p>If you need to extra me, feel free to send me an email.</p>
      <div class="extra-link-container">
        <p>
          <i class="fa fa-envelope email-icon" aria-hidden="true"></i>
          r3yi@ucsd.edu
        </p>
      </div>
    </div>
  </div>
</template>
<script>
  export default{
    name: 'ExtraPage'
  }
</script>

<style lang="scss" scoped>
  @import url('../styles/MainStyle.scss');
  #extra-page{
    #not-main-dialogue{
      text-align: center;
    }

    .extra-link-container{

      p{
        text-align: center;

        .email-icon{
          margin-right: 2%
        }
      }
    }
  }

</style>



