module.exports = [{"title":"ComPair","language":[false,false,false,false,true,true,true,true,true,true,false,false,false],"date":"September 2016 - December 2016","content":"ComPair is one of my first front-end projects. This is the final product of a UI/UX class. My friend, Jisu, and I made this website with the theme of \"collaboration\" in mind. Our goal was to help provide a service where students can inquire about a particular major or minor, and other students can provide information.\n\nIn the beginning, one of the hardest tasks for this project was to find a reliable way to utilize JSON objects. This was a requirement for our website. In the end, we deicded to make our project utilize a noSQL server with JSON objects. All of our data is saved in JSON objects, which is accessed by AJAX.\n\nTo write to our JSON files, we used PHP scripts, which was called with our AJAX calls.\n\nBesides that, our other requirement was to use elements of Bootstrap. Besides the container class and the col class, my friend and I styled everything else ourselves. We also used a lot of jQuery to help with events, and changing pages within our website.","url":"https://devsky.org/compair"},{"title":"BF Summon","language":[true,false,false,false,false,false,false,false,false,false,false,false,true],"date":"June 2015 - October 2015","content":"BF Summon Simulator, or Brave Frontier Summon Simulator, is my first attempted at making an Android app as well as showing my love for the first mobile gacha game I played. The idea behind this app is to create a way for my friends to help their \"gambling mindset\". The overall function of the app is complete, but extra relavant features such as adding all of the units from the game into the pool is not. I stored images of a few units into my Dropbox, and the images of the units are retrieved using sockets to get the images from my Dropbox.\n\nBesides just simulating summons, I wanted to be able to add my own units into the game. This was done by having the user fill out a form, which contains the relavant information needed for a unit. Afterwards, that unit is added into the pool of default units, and becomes available to summon. This feature was the hardest part for me because I struggled a lot with using implicit intents to open an image selector for the custom unit.\n\nI stopped working on this project because the core feature is complete. You can pull for a unit in the pool, and you can add units into the pool. Expanding on the base pool is very tedious because the game has over 300 units, meaning that I would have to get 300 images. Instead, I am currently working on another app with the same concept: simulating unit pulls. However, that app is still in the developing phase.","url":"https://github.com/RyanYi1/bfsim"},{"title":"Snap","language":[false,false,false,true,false,false,false,false,false,false,false,false,false],"date":"January 2017 - March 2017","content":"Snap is a Python application that takes input from a Neurosky Mindwave Mobile Headset, and executes a keyboard shortcut when the user's attention span exceeds a calibrated threshold. A GUI allows users to set the keyboard shortcut to execute when a user's attention span exceeds the threshold. This is another class project with my good friend Jisu, and our first taste of Brain-Computer Interaction.\n\nI really enjoyed working on the project because we were working with a headset. I thought that it was really cool how I could take a screenshot with my mind. This was also one of the first times I worked with Python.\n\nUnfortunately, the headset was rented, so I cannot continue further development. However, Jisu and I are interested in purchasing a headset to continue improving on Snap in the future. We'll do that when we have the money.","url":"https://github.com/RyanYi1/snap"},{"title":"ShallusBot","language":[false,false,false,true,false,false,false,false,false,false,false,false,false],"date":"December 2016 - Present","content":"ShallusBot is a discord bot created by my friend Cameron. He is an officer at 2 of UCSD's gaming divisions, and provides the utility of ShallusBot for his servers. The bot is based off of the \"Red Discord bot\", and has a developer team to work on its custom commands.\n\nI have contributed the Bracket commands to the bot. Our bot has the ability to track a tournament that is hosed on Challonge.com, and report the current status of a match. If the person in the server is also an admin, they have the ability to update scores from the bot as well! I plan to commands to get information about units in Fire Emblem Heros, which is a popular game in the servers that use ShallusBot.\n\nWorking on ShallusBot gave me my first taste of Python, as well as restful APIs. This has been one of my favorite projects to work on because I really enjoy contributing to a project that will help the communities that I am apart of. ","url":"https://github.com/RyanYi1/Shallus-Bot"},{"title":"CTones","language":[true,false,false,false,false,false,false,false,false,false,false,false,true],"date":"March 2015 - June 2015","content":"CTones, or CoupleTones, is an Android app that allows two users to partner up, and inform each other about the locations they have visited. This app is the final result of a class assignment. The app allows users to set markers on a Google Map to designate as favorite locations. Every time they get close enough to one of these locations, their partner will be informed. The purpose of this app is to provide a way for two people (specifically couples) to know the whereabouts of their partners.\n\nThis app was a bit fustrating to make because near the deadline for our final version, our team messed up the Github repository due forcing a push with outdated code. This broke our app, and scared me until we were able to get a working version, and turn it in. We couldn't get all of the bugs fixed, but our app demo was good enough.\n\nI still had a lot of fun making this app, despite the final moment. I learned a lot about using Firebase, as well as utilizing services and SharedPreferences.","url":null}]

