module.exports = [{"title":"Midterm season","date":"November 1, 2017","content":"Following up on my monthly news post, I have recently recovered from a busy week in school. Apparently it was also Halloween yesterday, but I couldn't tell since I as too busy worrying about midterms.\nLast week, I had 2 big assignments and a programming assignment for an Operating Systems class due on Friday. I am barely into my school quarter this year, and I already feel like I am being overwhelmed by homework. Unfortunatly the workload was too much. I had to drop some classes to help lossen my workload. However, the programming assignment took so long to do that I still felt super busy throughout the week. I also had 3 midterms this week to study for after my assignments were due. I will say that I had a scary Haloween because I didn't feel like I did good on any of my midterms.\nBesides school, I am also still working on getting a full time offer. I try my best to try and study for interviews in my spare time. Lately I feel like I have been doing better than last year in terms of getting interviews and coding challenges, so this makes me happy. Hopefully I will be able to update this site more. I am still working on redesigning the site and making it more responsive."},{"title":"Week 1","date":"October 6, 2017","content":"I am almost done with my first week of school, and a lot has happened since my last post. After I compmleted my internship, I went to school to start my training session for my customer support job at ITS Help Desk. This was about 2 weeks before school started, and I had a great time. The entire training session was a chance for all of the newcomers to learn about the tools that the Help Desk provides, as well as proper etiqutte and basic knowledge about UCSD's network. Since then, I have been having a fun time at my job. This job has been a great way to meet new people, and work on my communication skills.\n\nThe first week of class is also ending, and I feel fairly relaxed right now. However, this is the calm before the storm. I am expecting midterms to come in about 2 weeks, so I really need to start focusing on classes. Besides classes, the other important task I have is to continue developing and working on my portfolio and resume to try and find a job. Hopefully I will be able to fall back to the ITS Service Desk I have at the moment if I don't get anything, but I really want to get a job and be able to work at home."},{"title":"Plans","date":"September 12, 2017","content":"With the release of my website, I am also starting school soon. After I finish up my internship, I will be going back to UCSD a week before school starts to start training for a school job that I got. I am really happy to have the opportunity for this internship because I have a better understanding of the differences between a work and school environment. I always hated the school environment because of the pressure to do well on the scarce number of assignments and tests. However, I now know that school is designed with a safety net. School is set up so that even if you are doing bad, there is usually an opportunity to come back and get a good grade in the end. At work, it's much harder to rebound from big mistakes. Making a big mistake at work can stop the entire workflow of the company (I worked at a small start-up so this happened quite often). Besides that, since we have to build upon existing code, a mistake will always continue to cause trouble until it is taken care of.\n\nNow that I am coming back to school, I will appreciate the school environment more. I can't wait to go back to school. I am ready for the change in environment, and I can't wait to see my friends again."}]

