<template>
  <div id="app">
    <!--My navigation bar at the top of the site-->
    <table id="main-navbar">
      <tr>
        <td v-bind:class="{selected_option: selection==1}" @click="selection = 1;">
          <div class="navbar-option">
            <p>Home</p>
          </div>
        </td>
        <td v-bind:class="{selected_option: selection==2}" @click="selection = 2;">
          <div class="navbar-option">
            <p>Projects</p>
          </div>
        </td>
        <td v-bind:class="{selected_option: selection==3}" @click="selection = 3;">
          <div class="navbar-option">
            <p>Extra</p>
          </div>
        </td>
        <td v-bind:class="{selected_option: selection==4}" @click="selection = 4;">
          <div class="navbar-option">
            <p>My Life</p>
          </div>
        </td>
      </tr>
    </table>

    <div class="main-content">
      <!--These are the individual pages for the website.-->
      <HomePage v-show="selection==1"></HomePage>
      <ProjectPage v-show="selection==2"></ProjectPage>
      <ExtraPage v-show="selection==3"></ExtraPage>
      <MyLifePage v-show="selection==4"></MyLifePage>
    </div>

    <!--At the bottom of the website. Has links to my profiles at other sites, such as Github.-->
    <div class="footer-wrapper">
      <footer>
        <div id="footer-content">
          Site made by Ryan Yi
          <a href="https://www.facebook.com/ryan.yi2" target="_newtab"><span class="fa fa-facebook-square fa-2x"></span></a>
          <a href="https://twitter.com/RyanYi11" target="_newtab"><span class="fa fa-twitter fa-2x"></span></a>
          <a href="https://github.com/RyanYi1" target="_newtab"><span class="fa fa-github fa-2x"></span></a>
          <a href="https://www.linkedin.com/in/ryan-yi" target="_newtab"><span class="fa fa-linkedin fa-2x"></span></a>
        </div>
      </footer>
    </div>
  </div>
</template>

<script>
import HomePage from './components/HomePage.vue';
import ProjectPage from './components/ProjectPage.vue';
import ExtraPage from './components/ExtraPage.vue';
import MyLifePage from './components/MyLifePage.vue';

import 'font-awesome/css/font-awesome.css';

export default {
  name: 'app',
  components: {
    HomePage, ProjectPage, ExtraPage, MyLifePage
  },
  data(){
    return{
      selection: 1
    }
  }
}
</script>

<style lang="scss" scoped>
  html{
    height: 100%;
  }

  table { border-collapse:collapse }
  td{
    width: 20%;
  }

  td:hover{
    background-color: #92b8f4;
    height: 100%;
    p{
      font-size: 1.5rem !important;
      color: #dd3030 !important;
      text-shadow: -1px 0 #f42222, 0 1px #f42222, 1px 0 #f42222, 0 -1px #f42222 !important;
    }
  }
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #f7f4f4;
    text-align: center;
    width: 100%;
    overflow: hidden;

    display: flex;
    min-height: 100vh;
    flex-direction: column;

    #main-navbar{
      background-color: #6da5ff;
      height: 100%;
      width: 100%;
      cursor: pointer;
      cursor: hand;

      .navbar-option{
        text-align: center;
        p{
          font-size: 1.5rem;
          color: #e26c6c;
          text-shadow: -1px 0 #e54949, 0 1px #e54949, 1px 0 #e54949, 0 -1px #e54949;
        }
      }

      .selected_option{
        background-color: #99c0ff;
        height: 100%;
        p{
          font-size: 1.5rem;
          color: #dd3030;
          text-shadow: -1px 0 #f42222, 0 1px #f42222, 1px 0 #f42222, 0 -1px #f42222;
        }
      }
    }

    .main-content{
      flex: 1;
    }

    .footer-wrapper{
      display: flex;
      flex-direction: column;
      min-height: 100%;

      footer{
        width: 100%;
        background-color: #6da5ff;
        text-align: left;
        margin-top: 10%;
        height: 4%;
        flex: 1;

        a:visited{
          color:black;
        }
        a:link{
          color: black;
        }
        a:hover{
          color: #2d2d2d;
        }
        a:active{
          color: #2d2d2d;
        }

        #footer-content{
          padding-top: 2%;
          padding-bottom: 2%;
          padding-right: 2%;
          padding-left: 2%;
          font-size: 1rem;

        }
    }

      span{
        float: right;
        margin-left: 1%
      }
    }
  }
</style>



